import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_BOTTOM = mu.marginBottom();
const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

import Error from '../../components/error';
import Message from '../../components/message';

@observer
class Profile extends Component {
	constructor(props) {
        super(props);
        this.state = {
            time: Date.now(),
			count: 10
        };
    }

	resetEmailMessage = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailMessage', '');
		userStore.updateField('emailMessageIcon', '');
	}

	resetEmailError = () => {
		const { userStore } = this.props.stores;
		userStore.updateField('emailError', '');
		userStore.updateField('emailErrorIcon', '');
	}

	toggleSetting= async (field) => {
        const { userStore } = this.props.stores;
		const { user } = userStore;
		const { settings = {} } = user;
        await userStore.updateSetting({ [`settings.${field}`]: settings[field] === 1 ? 0 : 1 });
    }

	subscriptionCreate = async () => {
		const { paypalStore, userStore } = this.props.stores;
		this.setState({ paypalLoading: true });
		await paypalStore.subscriptionCreate({});
		await userStore.getInfo();
		this.setState({ paypalLoading: false });
	}

	subscriptionCancel = async (e) => {
		const { id } = e.target.closest('button').dataset;
		const { paypalStore, userStore } = this.props.stores;
		await paypalStore.subscriptionCancel({ planId: id });
		await userStore.getInfo();
	}

	subscriptionDetail = async () => {
		const { paypalStore, userStore } = this.props.stores;
		const { user = {} } = userStore;
		const { subscription } = user;

		if (subscription.payPalSubscriptionId) {
			const response = await paypalStore.subscriptionDetail({ id: subscription.payPalSubscriptionId });
			userStore.setSubscriptionPaypal(response.data);
			await userStore.getInfo();
		}
	}

	toggleCancel = () => {
		const { showCancel } = this.state;
		this.setState({
			showCancel: !showCancel,
		});
	}
	toggleDetails = () => {
		const { showDetails } = this.state;
		if (!showDetails) {
			this.subscriptionDetail();
		}
		this.setState({
			showDetails: !showDetails,
		});
	}

	componentDidMount() {
		const { userStore } = this.props.stores;

		if (this.props.verifyEmailToken) {
			userStore.verifyEmail({ token: this.props.verifyEmailToken });
		}
	}

	render() {
		const { userStore, appState } = this.props.stores;
		const { darkmode } = appState;
		const { user, emailMessage, emailError, emailMessageIcon, emailErrorIcon, isAdmin } = userStore;
		const { teams, settings = {} } = user;
		const allTeams = userStore.findTeams(teams);
		const apiServer = util.getApiServer();
		const jwtToken = util.getJwtToken();
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
				<div class='row mb-4 fixed-top' style={`margin-top: ${SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
					<div class='col text-center border-bottom border-primary subtopmenu text-overflow'>
						<nobr><a href='/users' class='text-primary stretched-link'><i class='fas fa-user' /> <Text id='users.title'>Din profil</Text></a></nobr>
					</div>
					<div class='col text-center subtopmenu text-overflow'>
						<nobr><a href='/users/settings' class='text-secondary stretched-link'><i class='fas fa-sliders-h' /> <Text id='users.settings'>Innstillinger</Text></a></nobr>
					</div>
					{isAdmin && <div class='col text-center subtopmenu text-overflow'>
						<nobr><a href='/users/subscription' class='text-secondary stretched-link'><i class='fas fa-signature' /> <Text id='users.subscriptions'>Abonnement</Text></a></nobr>
					</div>}
					<div class='col text-center subtopmenu text-overflow'>
						<nobr><a href='/users/integrations' class='text-secondary stretched-link'><i class='fas fa-plug' /> <Text id='users.integrations'>Integrasjoner</Text></a></nobr>
					</div>
				</div>

                <div class='row mt-5'>
					<div class='col-12'>
						<Error message={emailError} icon={emailErrorIcon} remove={this.resetEmailError} />
                        <Message message={emailMessage} icon={emailMessageIcon} remove={this.resetEmailMessage}  />

						<a href='/users/edit' class='btn btn-primary btn-sm float-right'>
							<Text id='users.edit'>Endre</Text>
						</a>

						<h5><Text id='users.title'>Din profil</Text></h5>
					</div>
					<div class='col-4 text-muted text-right'><Text id='users.email'>E-post</Text></div><div class='col-8'>{user.email}</div>

					<div class='col-4 text-muted text-right'><Text id='users.password'>Passord</Text></div><div class='col-8'>
						<a href='/users/edit-password' class='btn btn-primary btn-sm'>
							<Text id='users.change-password'>Endre passord</Text>
						</a>
					</div>
				
					{user.team && <><div class='col-4 text-muted text-right'><Text id='users.main-team'>Hovedteam</Text></div><div class='col-8'>{userStore.findTeam(user.team)}</div></>}
					{user.teams && <><div class='col-4 text-muted text-right'><Text id='users.all-teams'>Alle teamene</Text></div><div class='col-8'>{allTeams.map(team => (
						<div>
							{team.name}
							{/* <span class='text-muted'>
								<i class='fas fa-dog ml-3' /> {team.dogs.length}
								<i class='fas fa-users ml-3' /> {team.members.length}
							</span> */}
						</div>
					))}</div></>}
					{user.username && <><div class='col-4 text-muted text-right'><Text id='users.username'>Brukernavn</Text></div><div class='col-8'>{user.username}</div></>}
					{user.firstname && <><div class='col-4 text-muted text-right'><Text id='users.firstname'>Fornavn</Text></div><div class='col-8'>{user.firstname}</div></>}
					{user.lastname && <><div class='col-4 text-muted text-right'><Text id='users.lastname'>Etternavn</Text></div><div class='col-8'>{user.lastname}</div></>}
					{user.cellphone && <><div class='col-4 text-muted text-right'><Text id='users.cellphone'>Mobil</Text></div><div class='col-8'>{user.cellphone}</div></>}
					{user.url && <><div class='col-4 text-muted text-right'><Text id='users.homepage'>Hjemmeside</Text></div><div class='col-8'>{user.url}</div></>}
					{user.facebook && <><div class='col-4 text-muted text-right'><Text id='users.facebook'>Facebook</Text></div><div class='col-8'>{user.facebook}</div></>}
					{user.instagram && <><div class='col-4 text-muted text-right'><Text id='users.instagram'>Instagram</Text></div><div class='col-8'>{user.instagram}</div></>}
					{user.snapchat && <><div class='col-4 text-muted text-right'><Text id='users.snapchat'>Snapchat</Text></div><div class='col-8'>{user.snapchat}</div></>}
				</div>

				
				<div class='row mt-5 pt-5'>
					<div class='col-12 text-center'>
						<a href='/logout' class='btn btn-block btn-lg btn-warning'>
							<i class='fas fa-sign-out-alt' /> <Text id='header.logout'>Logg ut</Text>
						</a>
					</div>
				</div>

			</div>
		);
	}
}

export default Profile;
